  rotate: -10deg;
}

.ribbon {
  z-index: 1100;
  position: absolute;
  background-size: cover;
  color: white;
  right: 150px;
  cursor: pointer;
}

.ribbon4 {
  position: fixed;
  padding: 8px 10px;
  background: #cbb26b;
  -webkit-box-shadow: 0 0 9px 7px #4b6462;
  -moz-box-shadow: 0 0 9px 7px #4b6462;
