}

dialog::backdrop {
  background-color: rgba(75, 100, 98, 0.49);
}

dialog {
  border: none;
  box-shadow: #00000029 2px 2px 5px 0px;
  border-radius: 10px;
}

.sub-menu-item {
  transition: all 0.3s;
  /* Animation */
}

.sub-menu-item:hover {
  transform: scale(1.1);
